.problems{
    position: relative;

    .problems__container{
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        position: relative;
    }
}

p.problems__title{
    color: #fff;
    text-align: center;
    font-family: $DTW-Bold;
    font-size: vw(36);
    line-height: 120%;
    z-index: 5;
}

.problems__slogan{
    user-select: none;
    position: absolute;
    top: vw(350);
    right: vw(550);

    p{
        font-family: RFDewiExtended-Bold;
        text-transform: uppercase;
        font-size: vw(116);
        transform: rotate(-90deg);
        position: relative;
        color: rgba(216, 219, 222, 0.03);

        &.slogan__one{
            position: absolute;
        }

        &.slogan__two{
            position: absolute;
            top: vw(160);
            left: vw(120);
        }
    }
}

.problems__body{
    width: vw(691);
    height: vw(721);
    background: rgba(255, 255, 255, 0.08);
    box-shadow: 0px vw(51) vw(74) rgba(0, 0, 0, 0.25);
    border-radius: vw(41);
    position: relative;
    align-self: flex-end;
    margin-top: vw(80);
    margin-bottom: vw(100);
    z-index: 5;

    &::before{
        content: '';
        position: absolute;
        inset: 0;
        border-radius: vw(41);
        padding: vw(1);
        background: linear-gradient(179.22deg, rgba(175, 253, 255, 0) 0.7%, #AFFDFF 52.82%, rgba(175, 253, 255, 0) 100.77%),
        linear-gradient(89.92deg, rgba(175, 253, 255, 0) -1.09%, #AFFDFF 51.52%, rgba(175, 253, 255, 0) 99.92%);
        -webkit-mask: 
            linear-gradient(white 0 0) content-box, 
            linear-gradient(white 0 0);
        -webkit-mask-composite: xor;
        mask-composite: exclude;
    }

    .problems__body__decor{
        width: vw(290);
        height: vw(267);
        position: absolute;
        top: vw(-126);
        right: vw(-74);

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.problems__man{
    width: vw(1412);
    height: vw(940);
    position: absolute;
    top: vw(140);
    left: 0;
    z-index: 0;

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.cure__problems{
    align-self: center;
    text-align: center;
    z-index: 5;

    p.title{
        font-family: $DTW-Medium;
        font-size: vw(30);
        color: #fff;
        line-height: 140%;
    }

    p.text{
        font-family: $MS-Light;
        font-size: vw(18);
        line-height: 140%;
        color: rgba($color: #fff, $alpha: 0.9);
        width: vw(647);
        letter-spacing: vw(0.5);
        padding-top: vw(20);
    }
}

@media (max-width: 990px){
    .problems__man{
        display: none;
    }

    .problems__slogan{
        display: none;
    }

    .problems__body{
        width: vmin(356);
        height: vmin(580);
        background: rgba(255, 255, 255, 0.08);
        box-shadow: 0px vmin(51) vmin(74) rgba(0, 0, 0, 0.25);
        border-radius: vmin(41);
        position: relative;
        align-self: center;
        margin-top: vw(80);
        margin-bottom: vw(100);
        z-index: 5;
    
        &::before{
            content: '';
            position: absolute;
            inset: 0;
            border-radius: vmin(41);
            padding: vmin(1);
            background: linear-gradient(179.22deg, rgba(175, 253, 255, 0) 0.7%, #AFFDFF 52.82%, rgba(175, 253, 255, 0) 100.77%),
            linear-gradient(89.92deg, rgba(175, 253, 255, 0) -1.09%, #AFFDFF 51.52%, rgba(175, 253, 255, 0) 99.92%);
            -webkit-mask: 
                linear-gradient(white 0 0) content-box, 
                linear-gradient(white 0 0);
            -webkit-mask-composite: xor;
            mask-composite: exclude;
        }
    
        .problems__body__decor{
            width: vmin(130);
            height: vmin(131);
            position: absolute;
            top: vmin(-29);
            right: vmin(-27);
    
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    p.problems__title{
        color: #fff;
        text-align: center;
        font-family: $DTW-Medium;
        font-size: vmin(18);
        line-height: 120%;
        z-index: 5;
    }

    .cure__problems{
        align-self: center;
        text-align: center;
        z-index: 5;
    
        p.title{
            font-family: $DTW-Medium;
            font-size: vmin(14);
            color: rgba($color: #fff, $alpha: 0.85);
            line-height: 140%;
        }
    
        p.text{
            font-family: $MS-Light;
            font-size: vmin(14);
            line-height: 140%;
            color: rgba($color: #fff, $alpha: 0.9);
            width: vmin(356);
            letter-spacing: vmin(0.5);
            padding-top: vmin(10);
        }
    }
    
}